"use client";
import Navbar from "@/components/Navbar";
import getdata from "../../../../blogdata/getdata";
import { useEffect, useState } from "react";
export default async function PostPage({ params }) {

  const [blogs, setBlogs] = useState([])
  useEffect(() => {
    fetch(`/api/blogs/${params.id}`)
    .then((res) => res.json())
      .then((data) => setBlogs(data));
  },[])




  // ✅ Step 1: get all posts
  const blogPosts = await getdata();

  // ✅ Step 2: extract slug (no await needed)
  const { slug } = params;

  // ✅ Step 3: find matching post
  const post = blogPosts.find((p) => p.id === parseInt(slug));

  if (!post) {
    return (
      <div className="font-sans min-h-screen bg-gray-50 dark:bg-gray-900 text-gray-900 dark:text-white">
        <Navbar />
        <div className="max-w-4xl mx-auto py-20 px-6 text-center">
          <h1 className="text-3xl font-bold">404 | Post not found</h1>
        </div>
      </div>
    );
  }

  return (
    <div className="font-sans min-h-screen bg-gray-50 dark:bg-gray-900 text-gray-900 dark:text-white">
      <Navbar />

      {/* Post Header */}
      <header className="max-w-4xl mx-auto py-12 px-6 border-b border-gray-200 dark:border-gray-800">
        <h1 className="text-4xl font-extrabold">{post.title}</h1>
        <p className="mt-3 text-gray-600 dark:text-gray-400">By {post.author}</p>
      </header>

      {/* Post Content */}
      <main className="max-w-4xl mx-auto py-12 px-6 prose dark:prose-invert">
        {post.content.split("\n").map((para, i) => (
          <p key={i}>{para}</p>
        ))}
      </main>
    </div>
  );
}
